import { Box } from "@mui/material";
import { Link } from "react-router-dom";
import { PropsWithChildren } from "react";
import ALink from "react-anchor-link-smooth-scroll";
interface TextLinkProps extends PropsWithChildren {
  to: string;
}
export function TextLink(props: TextLinkProps) {
  return (
    <Box display="inline" color="#3376AA">
      <Link {...props} />
    </Box>
  );
}

interface AnchorLinkProps extends PropsWithChildren {
  href: string;
}
export function AnchorLink(props: AnchorLinkProps) {
  return (
    <Box display="inline" color="#3376AA">
      <ALink {...props} />
    </Box>
  );
}
